  position: sticky;
  top: 100px;
  transition: 0.3s;
}
.fixed-notscrolled {
  top: 132px;
  position: sticky;
}
.user-avatar {
  cursor: pointer;
  .user-camera {
    display: none;
  }

  &:hover {
    span:first-child {
      display: none;
    }
    .user-camera {
      display: block;
    }
  }
}

.delete-icon {
  position: absolute;
  visibility: hidden;
  bottom: 0;
  right: 0;
  z-index: 1111111 !important;
}

.new-avatar {
  position: absolute;
  visibility: hidden;
  bottom: 0;
  z-index: 2111111111;
  height: 100%;
  left: 0;
  background-color: var(--v-primary-base);
  color: #fff;
  cursor: pointer;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}

.avatar:hover {
  .display-icon {
    visibility: visible !important;
  }
}
</style>
